html{
  font-family: 'Montserrat', sans-serif;


}

body{
  
  margin: 0;
  padding: 0;
  height: 100vh;
  background:linear-gradient(#8cac8c, #667b66);
  background-size: cover;
  background-repeat: no-repeat;

}
h1 {
  color: #ffffff;
  text-align: center;
  font-size: 2rem;
}

.container {
  background:linear-gradient(#8c13bc, #ae0ba6);
  width: 26rem;
  height: 15rem;
  margin: 50px auto;
  border:3px solid #700e93;
   border-radius:22px;
}

.tree-col-container {
  display: grid;
  grid-template-columns: auto auto auto;
}

.error{
  text-align: center;
  font-size: 8px;
  color: #ffffff;
}

.result {
  margin: 3rem;
  text-align: center;
}
.btn button{
  box-shadow: 3px 2px #057e33;
  width: 3rem;
  height: 2rem;
}

.result p {
  font-size: 2rem;
  color: #ffffff;
  border: black solid 1px;
}

#total{
  margin-left: 2rem;
  box-shadow: 3px 2px #057e33;
  width: 6rem;
  height: 2rem;

}

#pagado{
  margin-left: 2rem;
  box-shadow: 3px 2px #057e33;
  width: 6rem;
  height: 2rem;
}